.container {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
  padding: 0;
}

.experienceTimeline {
  position: relative;
  max-width: 1200px;
  margin: 0 auto;
  width: 100%;
  padding: 20px;
}

.experienceTimeline::before {
  content: '';
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  width: 2px;
  height: 100%;
  background: var(--gradient-primary);
  opacity: 0.3;
  top: 0;
}

.experienceCard {
  position: relative;
  background: var(--secondary-bg);
  border-radius: 16px;
  padding: 30px;
  margin-bottom: 40px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border-color);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  width: calc(50% - 40px);
  text-align: left;
}

.experienceCard:nth-child(odd) {
  margin-left: 0;
}

.experienceCard:nth-child(even) {
  margin-left: calc(50% + 40px);
}

.experienceCard::before {
  content: '';
  position: absolute;
  width: 20px;
  height: 20px;
  background: var(--gradient-primary);
  border-radius: 50%;
  top: 35px;
  box-shadow: 0 0 0 6px var(--background-color);
}

.experienceCard:nth-child(odd)::before {
  right: -50px;
}

.experienceCard:nth-child(even)::before {
  left: -50px;
}

.experienceCard:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
}

.companyName {
  font-size: 24px;
  font-weight: 700;
  color: var(--text-color);
  margin-bottom: 8px;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.duration {
  font-size: 16px;
  color: var(--btn-color);
  font-weight: 600;
  margin-bottom: 15px;
  display: flex;
  align-items: center;
  gap: 8px;
}

.duration::before {
  content: '';
  width: 4px;
  height: 4px;
  background: var(--btn-color);
  border-radius: 50%;
  display: inline-block;
}

.description {
  font-size: 16px;
  color: var(--text-secondary);
  line-height: 1.8;
  margin: 0;
}

.companyLink {
  text-decoration: none;
  color: inherit;
  display: block;
  transition: none;
}

.viewCompany {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  margin-top: 20px;
  color: var(--btn-color);
  font-weight: 600;
  font-size: 14px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  transition: gap 0.3s ease;
}

.viewCompany::after {
  content: 'â†’';
  transition: transform 0.3s ease;
}

.experienceCard:hover .viewCompany {
  gap: 12px;
}

.experienceCard:hover .viewCompany::after {
  transform: translateX(4px);
}

@media (max-width: 768px) {
  .experienceTimeline::before {
    left: 20px;
  }
  
  .experienceCard {
    width: calc(100% - 40px);
    margin-left: 40px !important;
  }
  
  .experienceCard::before {
    left: -30px !important;
    right: auto !important;
  }
}

@media (width >= 800px) {
  .experienceCard {
    padding: 40px;
  }
  
  .companyName {
    font-size: 28px;
  }
  
  .duration {
    font-size: 18px;
  }
  
  .description {
    font-size: 18px;
  }
}

@media (width >= 1200px) {
  .experienceTimeline {
    padding: 40px;
  }
  
  .experienceCard {
    margin-bottom: 60px;
  }
}