:root {
  --background-color: #ffffff;
  --secondary-bg: #f8f9fa;
  --btn-color: #0066ff;
  --btn-hover-color: #0052cc;
  --btn-text-color: #ffffff;
  --accent-color: #ff6b6b;
  --project-card-bg: #ffffff;
  --text-color: #1a1a1a;
  --text-secondary: #6c757d;
  --form-text-color: #495057;
  --footer-text-color: #6c757d;
  --border-color: #e9ecef;
  --transition-duration: 0.3s;
  --border-radius: 12px;
  --shadow-sm: 0 2px 4px rgba(0, 0, 0, 0.06);
  --shadow: 0 4px 16px rgba(0, 0, 0, 0.08);
  --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.12);
  --gradient-primary: linear-gradient(135deg, #0066ff 0%, #4d94ff 100%);
  --gradient-accent: linear-gradient(135deg, #ff6b6b 0%, #ff8787 100%);
}

[data-theme='dark'] {
  --background-color: #0a0a0a;
  --secondary-bg: #1a1a1a;
  --btn-color: #4d94ff;
  --btn-hover-color: #6ba3ff;
  --btn-text-color: #ffffff;
  --accent-color: #ff8787;
  --project-card-bg: #1a1a1a;
  --text-color: #ffffff;
  --text-secondary: #b3b3b3;
  --form-text-color: #e0e0e0;
  --footer-text-color: #999999;
  --border-color: #2a2a2a;
  --shadow-sm: 0 2px 4px rgba(0, 0, 0, 0.2);
  --shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
  --shadow-lg: 0 8px 32px rgba(0, 0, 0, 0.4);
  --gradient-primary: linear-gradient(135deg, #4d94ff 0%, #6ba3ff 100%);
  --gradient-accent: linear-gradient(135deg, #ff8787 0%, #ffa3a3 100%);
}

* {
  box-sizing: border-box;
}

body {
  background-color: var(--background-color);
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  transition: background-color var(--transition-duration) ease;
  scroll-behavior: smooth;
  line-height: 1.6;
  position: relative;
}

body::before {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  background: radial-gradient(ellipse at top, var(--secondary-bg) 0%, transparent 70%),
              radial-gradient(ellipse at bottom, var(--accent-color) 0%, transparent 70%);
  opacity: 0.05;
  pointer-events: none;
  z-index: -1;
}

#root {
  display: flex;
  flex-direction: column;
  gap: 80px;
  width: 100%;
  max-width: 1200px;
  padding: 40px 20px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

h1,
h2,
h3,
h4,
p {
  color: var(--text-color);
  margin: 0;
  padding: 0;
  transition: color var(--transition-duration) ease;
  font-weight: 400;
}

h2,
h3,
h4,
p {
  font-family: 'Roboto Mono', monospace;
}

h1,
h2 {
  text-transform: uppercase;
  letter-spacing: 1px;
}

h2 {
  font-size: clamp(24px, 3vw, 32px);
  font-weight: 600;
}

h3 {
  font-size: clamp(20px, 2.5vw, 24px);
  font-weight: 600;
}

h1 {
  font-family: 'Rubik', sans-serif;
  font-size: clamp(36px, 5vw, 56px);
  font-weight: 700;
  letter-spacing: -0.02em;
  line-height: 1.1;
}

p {
  font-size: clamp(16px, 1.5vw, 18px);
  font-weight: 400;
  color: var(--text-secondary);
  line-height: 1.7;
}

.sectionTitle {
  margin-bottom: 40px;
  position: relative;
  text-align: center;
  font-size: clamp(28px, 4vw, 40px);
  font-weight: 700;
  background: var(--gradient-primary);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

.sectionTitle::after {
  content: '';
  display: block;
  width: 80px;
  height: 4px;
  background: var(--gradient-primary);
  margin: 20px auto 0;
  border-radius: 2px;
  transition: width var(--transition-duration) ease;
}

.sectionTitle:hover::after {
  width: 120px;
}

.hover {
  cursor: pointer;
  transition: all var(--transition-duration) cubic-bezier(0.4, 0, 0.2, 1);
  border-radius: var(--border-radius);
  position: relative;
  overflow: hidden;
}

.hover::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.1);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}

.hover:hover::before {
  width: 300px;
  height: 300px;
}

.hover:hover {
  transform: translateY(-3px);
  box-shadow: var(--shadow-lg);
}

.hover:focus {
  outline: 2px solid var(--btn-color);
  outline-offset: 2px;
}

/* Button improvements */
button {
  background: var(--gradient-primary);
  color: var(--btn-text-color);
  border: none;
  padding: 14px 32px;
  border-radius: 50px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all var(--transition-duration) cubic-bezier(0.4, 0, 0.2, 1);
  box-shadow: 0 4px 16px rgba(0, 102, 255, 0.2);
  position: relative;
  overflow: hidden;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

button::before {
  content: '';
  position: absolute;
  top: 0;
  left: -100%;
  width: 100%;
  height: 100%;
  background: linear-gradient(90deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transition: left 0.7s;
}

button:hover::before {
  left: 100%;
}

button:hover {
  transform: translateY(-3px) scale(1.02);
  box-shadow: 0 8px 24px rgba(0, 102, 255, 0.3);
}

button:focus {
  outline: none;
  box-shadow: 0 0 0 3px rgba(0, 102, 255, 0.3);
}

button:active {
  transform: translateY(-1px) scale(1);
}

/* Loading animation for images */
img {
  transition: opacity var(--transition-duration) ease;
}

img:not([src]) {
  opacity: 0;
}

/* Improved link styles */
a {
  transition: all var(--transition-duration) ease;
  text-decoration: none;
}

a:focus {
  outline: 2px solid var(--btn-color);
  outline-offset: 2px;
  border-radius: var(--border-radius);
}

@media (width >= 800px) {
  #root {
    gap: 120px;
    padding: 60px 40px;
  }

  .sectionTitle {
    margin-bottom: 60px;
  }
}

@media (width >= 1400px) {
  #root {
    gap: 160px;
    padding: 80px 60px;
  }

  .sectionTitle {
    margin-bottom: 80px;
  }
}

/* Animations */
@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes slideInLeft {
  from {
    opacity: 0;
    transform: translateX(-50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes slideInRight {
  from {
    opacity: 0;
    transform: translateX(50px);
  }
  to {
    opacity: 1;
    transform: translateX(0);
  }
}

/* Scroll animations */
.animate-on-scroll {
  opacity: 0;
  animation: fadeInUp 0.8s ease forwards;
}

.animate-delay-1 {
  animation-delay: 0.1s;
}

.animate-delay-2 {
  animation-delay: 0.2s;
}

.animate-delay-3 {
  animation-delay: 0.3s;
}

/* Smooth scroll behavior */
html {
  scroll-behavior: smooth;
}

/* Reduce motion for users who prefer it */
@media (prefers-reduced-motion: reduce) {
  * {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
  
  html {
    scroll-behavior: auto;
  }
  
  .hover:hover {
    transform: none;
  }
  
  button:hover {
    transform: none;
  }
}
