.container {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
}

.projectsContainer {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  width: 100%;
  padding: 0 20px;
  justify-items: center;
}

.projectCard {
  display: flex;
  flex-direction: column;
  width: 100%;
  max-width: 350px;
  text-decoration: none;
  background: var(--project-card-bg);
  border-radius: 16px;
  overflow: hidden;
  box-shadow: var(--shadow);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  border: 1px solid var(--border-color);
  position: relative;
}

.projectCard::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--gradient-primary);
  transform: translateX(-100%);
  transition: transform 0.3s ease;
}

.projectCard:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-lg);
}

.projectCard:hover::before {
  transform: translateX(0);
}

.projectImage {
  width: 100%;
  height: 200px;
  object-fit: cover;
  background-color: var(--secondary-bg);
  border-radius: 0;
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}

.projectCard:hover .projectImage {
  transform: scale(1.05);
}

.projectInfo {
  padding: 20px;
  text-align: left;
}

.projectTitle {
  margin: 0 0 10px;
  font-size: 22px;
  font-weight: 600;
  color: var(--text-color);
  transition: color 0.3s ease;
}

.projectCard:hover .projectTitle {
  color: var(--btn-color);
}

.projectDescription {
  font-size: 16px;
  color: var(--text-secondary);
  margin: 0;
  line-height: 1.6;
}

.techStack {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: 15px;
}

.techTag {
  padding: 4px 12px;
  background: var(--secondary-bg);
  border-radius: 20px;
  font-size: 14px;
  color: var(--text-secondary);
  border: 1px solid var(--border-color);
}

@media (width >= 800px) {
  .projectsContainer {
    grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
    gap: 40px;
    padding: 0;
  }

  .projectCard {
    max-width: 380px;
  }

  .projectImage {
    height: 240px;
  }
}

@media (width >= 1200px) {
  .projectsContainer {
    grid-template-columns: repeat(3, 1fr);
    gap: 50px;
  }
}