.container {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 100%;
}

.skillCategories {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  gap: 30px;
  width: 100%;
  margin-top: 20px;
}

.skillCategory {
  background: var(--secondary-bg);
  border-radius: 16px;
  padding: 30px;
  box-shadow: var(--shadow);
  border: 1px solid var(--border-color);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
  position: relative;
  overflow: hidden;
}

.skillCategory::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 4px;
  background: var(--gradient-primary);
  transform: scaleX(0);
  transform-origin: left;
  transition: transform 0.3s ease;
}

.skillCategory:hover {
  transform: translateY(-5px);
  box-shadow: var(--shadow-lg);
}

.skillCategory:hover::before {
  transform: scaleX(1);
}

.categoryTitle {
  font-size: 20px;
  font-weight: 600;
  color: var(--text-color);
  margin-bottom: 20px;
  text-transform: uppercase;
  letter-spacing: 1px;
}

.skillList {
  display: flex;
  flex-direction: column;
  gap: 15px;
  align-items: flex-start;
  text-align: left;
}

.skillItem {
  display: flex;
  align-items: center;
  gap: 12px;
  width: 100%;
  padding: 10px 15px;
  background: var(--background-color);
  border-radius: 8px;
  transition: all 0.2s ease;
  border: 1px solid transparent;
}

.skillItem:hover {
  border-color: var(--btn-color);
  transform: translateX(5px);
}

.skillItem img {
  width: 20px;
  height: 20px;
  flex-shrink: 0;
}

.skillItem p {
  margin: 0;
  font-size: 16px;
  color: var(--text-color);
  font-weight: 500;
}

.skillLevel {
  margin-left: auto;
  display: flex;
  gap: 4px;
}

.levelDot {
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background: var(--border-color);
  transition: background 0.3s ease;
}

.levelDot.filled {
  background: var(--gradient-primary);
}

@media (width >= 800px) {
  .skillCategories {
    grid-template-columns: repeat(3, 1fr);
    gap: 40px;
  }
  
  .categoryTitle {
    font-size: 22px;
  }
  
  .skillItem p {
    font-size: 18px;
  }
}

@media (width >= 1200px) {
  .skillCategories {
    gap: 50px;
  }
  
  .skillCategory {
    padding: 40px;
  }
}