.container {
  position: relative;
  text-align: center;
  padding: 60px 20px 40px;
  margin-top: 80px;
  background: var(--secondary-bg);
  border-top: 1px solid var(--border-color);
}

.container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100px;
  height: 3px;
  background: var(--gradient-primary);
  border-radius: 2px;
}

.footerContent {
  max-width: 600px;
  margin: 0 auto;
}

.footerText {
  font-size: 16px;
  color: var(--footer-text-color);
  line-height: 1.8;
  margin: 0;
}

.footerLinks {
  display: flex;
  gap: 30px;
  justify-content: center;
  margin-top: 30px;
}

.footerLink {
  color: var(--footer-text-color);
  text-decoration: none;
  font-size: 14px;
  transition: color 0.3s ease;
  position: relative;
}

.footerLink::after {
  content: '';
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: var(--gradient-primary);
  transition: width 0.3s ease;
}

.footerLink:hover {
  color: var(--btn-color);
}

.footerLink:hover::after {
  width: 100%;
}

.copyright {
  margin-top: 30px;
  font-size: 14px;
  color: var(--footer-text-color);
  opacity: 0.8;
}

@media (width >= 800px) {
  .container {
    padding: 80px 40px 60px;
  }
  
  .footerText {
    font-size: 18px;
  }
  
  .footerLinks {
    gap: 40px;
  }
  
  .footerLink {
    font-size: 16px;
  }
}